import React from 'react';
import { Link } from 'react-router-dom'
import '../styles/Navbar.css';
// ReactStrap Imports 
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from 'reactstrap';
import MyLogo from "../images/chlogo.png";







export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }
  render() {
    return (
<section> 


 <div className="hideonmobile"> 
        <Nav right>
        <NavbarBrand href="/" className="mr-auto">  <img className="MyLogo" src={MyLogo}  alt="CH Logo" /></NavbarBrand>
          <NavItem>
          <NavLink color="#black;" to='/Home'>  <Link to='/'><b><h1>Home</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Cyber'>  <Link to='/Cyber'><b><h1>Cyber Security</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Linux'>  <Link to='/Linux'><b><h1>Linux</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Portfolio'>  <Link to='/Portfolio'><b><h1>Portfolio</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='Contact'>  <Link to='/Contact'><b><h1>Contact</h1></b></Link></NavLink>
          </NavItem>
     
        </Nav>
</div> 
        <Navbar className="mobile" color="black" light>
         
        
        
          <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />

            <img className="MyLogo2" src={MyLogo}  alt="CH Logo" />
          <Collapse isOpen={!this.state.collapsed} navbar>
         
            <Nav navbar>
              <NavItem> 
            <NavLink color="#black;" to='/Home'>  <Link to='/'><b><h1>Home</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Cyber'>  <Link to='/Cyber'><b><h1>Cyber Security</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Linux'>  <Link to='/Linux'><b><h1>Linux</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='/Portfolio'>  <Link to='/Portfolio'><b><h1>Portfolio</h1></b></Link></NavLink>
          </NavItem>
          <NavItem>
          <NavLink to='Contact'>  <Link to='/Contact'><b><h1>Contact</h1></b></Link></NavLink>
          </NavItem>
             
            </Nav>
          </Collapse>
          
         
        </Navbar>
      
      </section> 
    );
  }
}



